.title {
    display: table;
    margin: 20px auto;
}

.container {
    border: 1px solid #000;
    width: 550px;
    height: 400px;
    margin: auto;
}

.container > h1 {
    margin: 30px auto;
    display: table;
}

img {
    max-width: 550px;
    max-height: 400px;
}

input {
    font-family: 'Indie Flower', cursive !important;
    background: url(../img/Box.svg) no-repeat;
    border: none;
    padding: 15px;
}

input:focus {
    outline: none;
}

button {
     background: none;
     border: none;
     font-family: 'Indie Flower', cursive;
     font-size: 16px;
     cursor: pointer;
 }

button:focus, button:hover {
    outline: none;
    background-color: #e5e5e5;
}


